body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --roboto: Roboto;
  --font-rock-salt: "Rock Salt";
  --font-zeyada: Zeyada;
  --font-titan-one: "Titan One";
  --font-roboto-condensed: "Roboto Condensed";

  /* font sizes */
  --font-size-lgi: 19px;
  --font-size-mid: 17px;
  --font-size-45xl: 64px;
  --font-size-base: 16px;
  --font-size-13xl: 32px;
  --font-size-5xl: 24px;
  --roboto-size: 20px;

  /* Colors */
  --color-white: #fff;
  --color-gray: #738d85;
  --color-dimgray: #4c5a67;
  --color-thistle: #c3a9b5;
  --color-darkslategray: #456e6d;
  --color-whitesmoke: rgba(245, 245, 245, 0);

  /* border radiuses */
  --br-31xl: 50px;
  --br-8xs: 5px;
  --br-3xs: 10px;
  --br-7xs: 6px;
}
